    background-color: transparent;
}

.mywork_projectDescription {
    font-size: 13px;
    margin: 1%;
    background-color: transparent;
}

.mywork_projectStack {
    font-size: 12px;
    margin: 1%;
    background-color: transparent;
    color: lightskyblue;
    padding-top: 50px;
}

.mywork_project:hover > .mywork_projectStack {
    color: #1a2330;
}

.mywork_button {
    text-align: right;
    margin: 1%;
    position: relative;
    top: -25px;
    background-color: transparent !important;
}

.mywork_buttonGithub {
    margin: 1%;
    background-color: transparent;
}

.mywork_buttonLink {
    margin: 1%;
    background-color: transparent;
}

.mywork_buttonGithub:hover {
    color: #ccff15;
}

.mywork_buttonLink:hover {
